"use client";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { ScrollArea } from "@/components/ui/scroll-area";

interface SidebarProps {
  isOpen: boolean;
}

const containers = [
  { name: "20ft Standard", description: "Perfect for smaller shipments" },
  { name: "40ft Standard", description: "Ideal for larger cargo volumes" },
  { name: "40ft High Cube", description: "Extra height for taller items" },
  {
    name: "Refrigerated",
    description: "Temperature-controlled for perishables",
  },
  { name: "Open Top", description: "Easy loading for oversized cargo" },
  {
    name: "Flat Rack",
    description: "Designed for heavy machinery and vehicles",
  },
];

export function Sidebar({ isOpen }: SidebarProps) {
  if (!isOpen) return null;

  return (
    <Card className="w-64 h-full">
      <CardHeader>
        <CardTitle>Our Containers</CardTitle>
        <Button className="w-full mt-2">My Containers</Button>
      </CardHeader>
      <CardContent>
        <ScrollArea className="h-[calc(100vh-180px)]">
          {containers.map((container, index) => (
            <div key={index} className="mb-4">
              <h3 className="font-semibold">{container.name}</h3>
              <p className="text-sm text-gray-600">{container.description}</p>
            </div>
          ))}
        </ScrollArea>
      </CardContent>
    </Card>
  );
}
